<template>
  <div class="dialogue-info">
    <header id="wx-header">
      <!--<div class="other"><span>添加朋友</span></div>-->
      <div class="center">
        <div class="iconfont icon-return-arrow" v-on:click="$router.back()">
          <span>返回</span>
        </div>
        <span>设置备注</span>
        <div style="float:right;margin-right:5px;">
          <van-button type="primary" size="small" @click="confirm">完成</van-button>
        </div>
      </div>
    </header>
    <div style="padding:20px 0 10px 10px;color:#737475">备注名</div>
    <van-cell-group>
      <van-field v-model="value" placeholder="请输入备注" />
    </van-cell-group>
  </div>
</template>
<script>
import { getMessage, send } from "../../socket/socket";
import qs from "qs";
export default {
  data() {
    return {
      value: this.$route.query.remark
    };
  },
  mounted() {
    // console.log(this.$store.state.allContacts);
  },
  methods: {
    confirm() {
      this.$http
        .post(
          "/api/friend/editFriendRemark",
          qs.stringify({
            friendUserId: this.$route.query.userId,
            friendRemark: this.value
          })
        )
        .then(res => {
          if (res.data.code === 200) {
            this.$store.state.allContacts.map(item => {
              if (item.user.userId === this.$route.query.userId) {
                item.friendRemark = this.value;
              }
            });
            this.$router.back();
          }
        });
    }
  }
};
</script>
<style>
</style>


